body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
  background-color: black;
  color: white
}

#root{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.page{
  padding: 76px 15px 0 15px;
  flex-grow: 1;
}

.about{
  margin-bottom: 15px;
}

.big-heading{
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 15px;
}

.heading{
  font-weight: bold;
  font-size: 20px;
}

.pic{
  width: 100%;
}

.card{
  margin-bottom: 15px;
}

.card-heading{
  font-weight: bold;
  font-size: 25px;
}

.card-body{
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.card-when{
  color: #888888
}

.socials{
  display: flex;
  justify-content: center;
  gap: 5px;
  margin-top: 35px;
}

.icon-big{
  max-height: 50px;
}

.icon{
  height: 20px;
  width: 20px;
  cursor: pointer;
}

.footer{
  background-color: black;
  color: white;
  display: flex;
  align-items: center;
  font-size: 12px;
  padding: 10px;
  justify-content: space-between;
}

.ext-link{
  display: flex;
  align-items: center;
  gap: 3px
}

.to-eventbrite {
  background-color: #ec1f24; /* Green */
  border: none;
  width: 100%;
  margin-bottom: 15px;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.venues{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}